/* second page */
body {
    position: relative;
}

.second-page {
    width: 100%;
    position: absolute;
    top: -100%;
    opacity: 0;
    // display: none;
    background: #fff;
    transition: 1s all; 
    margin-top: 130px;

    .col-6-12 {
        padding-left: 0;
        padding-right: 0;
    }

}


.second-page__box{
    margin: 0 auto;
    height: 750px;
    width: 95%;
    background: rgba(240, 221, 221, 0.5);
    border: 2px solid white;
    box-shadow: 3px 3px 15px 4px  rgba(226, 221, 188, 0.904);
    position: relative;

}

// exit

.fa {
    font-size: 30px;
    position: absolute;
    top:0;
    left: 96%;
    margin-left: 5px;
    color: #333;
    z-index: 3;
}

//both boxes
.second-page__list {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 750px;

  

}

.second-page__title{
    color: $fontColor;
    font-weight: 800;
    text-transform: uppercase;
    z-index: 1;
    font-size: 25px;
}

//bottom box weather
.second-page__list--weather{
    width: 270px;
    height: 270px;
    padding: 10px;
    border-radius: 50%;
    position: absolute;
    top: 90%;
    left: 20%;
    opacity: 0;
    text-align: left;
    display: none;
    // display: flex;
    // flex-direction: column;
    // justify-content: center;
    // align-items: center;
}

.second-page__list--right {
    list-style: none;
    margin: 0;

}

.second-page__title--small{
    text-align: center;
    font-size: 15px;
    z-index: 1;
    text-transform: uppercase;
    margin-top: 30px;
}

.elRightList{
    padding: 5px;
    font-size: 10px;
    color: $fontColor;
    font-weight: 600;
}

