.night {
    background: url('../images/nature-milky-way-night-stars-102539 (1).jpeg') repeat-x;
    background-size: cover;
    position: absolute;
    transition: 2s all;
    animation: moveObject linear 20s infinite;
}


.day {
    background: url('../images/day.jpeg') repeat-x;
    background-size: cover;
    position: absolute;
    opacity: 0.9;
    transition: 2s all;
    animation: moveObject linear 20s infinite;
}

@keyframes moveObject{
    from {background-position: 0, 0;}
    to {background-position: 100%, 0;}
}


.btnAdd {
    text-align: left;
    padding: 10px 10px;
    margin-top: 30px;
    text-align: center;
}


.icon {
    width: 80px;
    height: 80px;
    display: inline-block;
    position: absolute;
    bottom: -70px;
    left: 40%;
    transform: translate(-15%, -80%);
}

.time {
    font-size: 14px;
    color: rgb(97, 20, 20);
    text-align: center;
    font-weight: 600;
    margin: 0;
}




//left box
.second-page__list--left{
    list-style: none;
    background: rgba(240, 221, 221, 0.9);
    box-shadow: 3px 3px 5px #333;
    opacity: 0.9;
    padding: 10px;
}



.elInfo {
    padding: 20px;
    font-size: 20px;
    color: $fontColor;
    font-weight: 600;
  
}

.leftBoxBackground {
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
}


