//large map

@media screen and (min-width: $smallphones){
    .firstMap {
        transform: scale(0.36);
        margin-top: 0;
        margin-left: 40px; 
    }
    .main-banner__nav__title {
        height: 10px;
    }

    .btn {
        margin-top: 10px;
    }
}

@media screen and (min-width: $smartphones){
    .firstMap {
        transform: scale(0.7);
        margin-top: 100px;  
    }

    .main-banner__map{
        max-height: 700px;
    }

    .main-banner__nav {
        width: auto;
        flex-direction: row;
        margin-top: 20px;
        margin-bottom: 30px;
    }

    .main-banner__nav__title {
        height: 20px;
    }

    .btn {
        padding: 15px 10px;
    }
    .main-banner__header {
        margin: 0 auto;
    }


}

@media screen and (min-width: $tablets){
    .firstMap {
        transform: scale(0.9);
    }

    .main-banner__map{
        max-height: 900px;
    }

    .main-banner {
        display: flex;
        align-items: flex-end;
    }

    .main-banner__nav__title {
        height: 30px;
    }

    .btn {
        padding: 20px 10px;
    }


}

@media screen and (min-width: $desktops){
    .firstMap {
        transform: scale(1.15);
    }

    .main-banner__map{
        max-height: 1200px;
    }
    .main-banner__nav__title {
        width: 1000px;
    }


}


