@media screen and (min-width: $smartphones){
    .second-page {
        margin-top: 50px;
    }

    .second-page__list--weather {
        width: 350px;
        height: 350px;
        padding: 20px;
        top: 90%;
        left: 20%;
    }
    .second-page__title--small{
        font-size: 20px;
    }
    .elRightList {
        font-size: 15px;
        padding: 10px;
    }

}
@media screen and (min-width: $tablets){
    .second-page {
        margin-top: 0;
    }
    .second-page__list--weather {
        transform: scale(0.6);
    }
    .second-page__list--weather {
        width: 600px;
        height: 600px;
        top: 60%;
        left: 30%;
     }
     .second-page__title--small{
        font-size: 30px;
        margin-top: 50px;
    }

    .time {
        font-size: 25px;
    }
    .elRightList {
        font-size: 26px;
        padding: 10px;
    }

    .icon {
        width: 100px;
        height: 100px;
    }
}

@media screen and (min-width: $desktops){
    .second-page__list--weather {
        transform: scale(0.7);
    }
}